/* ==========================================================================
   GRID
   ========================================================================== */

// Container
.container {
    @include container-fixed;
    max-width:$container-large;
    @include respon-to($screen-large) {
        max-width: $container-medium;
    }
    @include respon-to($screen-medium) {
        max-width: $container-small;
    }
    @include respon-to($screen-small) {
        max-width: $container-extra-small;
    }
}

// Fluid container
.container-fluid {
    @include container-fixed;
}

// Row
.row {
    @include make-row;
}
.row-no-padding {
    @include make-row;
    > [class*="col-"] {
        padding-left: 0;
        padding-right: 0;
    }
}

// Columns
@include make-grid-columns;
@include make-grid(lg);
@media only screen and (max-width: $screen-medium) {
    @include make-grid(md);
}
@media only screen and (max-width: $screen-small) {
    @include make-grid(sm);
}
@media only screen and (max-width: $screen-extra-small) {
    @include make-grid(xs);
}