/* ==========================================================================
   LIST
   ========================================================================== */

ul,ol{
    margin:0 0 2rem;
    padding:0;
    li{
        margin:0;
        padding:0 0 0 0.8rem;
        font-size:2.1rem;
        line-height:1.38;
        letter-spacing:0.16px;
        font-weight:$reguler;
    }
    ul,ol{
        margin-bottom: 0;
    }
}

ol{
    margin-left:2rem;
    list-style-type:decimal;
    ol{
        list-style-type:lower-alpha;
        ol{
            list-style-type:decimal;
            ol{
                list-style-type:lower-alpha;
                ol{
                    list-style-type:decimal;
                }
            }
        }
    }
}

ul{
    margin-left:2.4rem;
    list-style-type:square;
    li{
        padding-left:0.4rem;
    }
    ul{
        list-style-type:disc;
        ul{
            list-style-type:square;
            ul{
                list-style-type:disc;
                ul{
                    list-style-type:square;
                }
            }
        }
    }
}