/* ==========================================================================
   FORM
   ========================================================================== */

.welcome-form{
    padding-top:2rem;
}

.input-row{
    position:relative;
    margin-bottom:25px;
    padding-top:10px;
    &:last-child{
        margin-bottom: 0;
    }
    &.has-input{
        .input-label{
            @include transform(translateY(-16px) scale(0.75));
        }
    }
}

.input-label{
    font-size:1.6rem;
    line-height:1.2;
    color:$input-label;
    position:absolute;
    top:18px;
    left:0;
    @include transform(translateY(0) scale(1));
    @include transition(all .25s ease);
    @include transform-origin(0 0);
}

.input-text{
    border:0;
    outline:0;
    background-color:transparent;
    width:100%;
    padding:10px 0;
    font-size:1.6rem;
    border-bottom:solid 1px $line;
    color:$text;
    @include border-radius(0);
    @include box-shadow(none);
    &:focus{
        background-color:transparent;
        + .input-label{
            @include transform(translateY(-16px) scale(0.75));
        }
    }
}

input:-webkit-autofill {
    -webkit-box-shadow:0 0 0 30px #fff inset;
}

.error-message{
    color:$error-message;
    font-size:1.4rem;
    line-height:1.42;
    letter-spacing:0.05px;
    margin:8px 0;
    opacity:0;
    visibility:hidden;
    @include transition(all .15s ease);
    &.show{
        opacity:1;
        visibility:visible;
    }
    @include respon-to($screen-small) {
        margin:0;
    }
}

.input-search{
    background:transparent url(../images/icon/search.svg) no-repeat 97.5% center;
    padding-right:40px;
    white-space:nowrap; 
    overflow:hidden;
    text-overflow:ellipsis;
    @include respon-to($screen-retina) {
        background-size:22px auto;
    }
}

.input-password{
    border:0;
    outline:0;
    background-color:transparent;
    width:100%;
    padding:10px 0;
    font-size:2.1rem;
    line-height:2;
    font-weight:$bold;
    color:$white;
    text-align:center;
    letter-spacing:15px;
    @include border-radius(0);
    @include box-shadow(none);
}