/* ==========================================================================
   OTHER
   ========================================================================== */

.page-list{
    margin:0;
    padding:0;
    list-style:none;
    li{
        margin:0 0 1.2rem;
        padding:0;
        &:last-child{
            margin-bottom:0;
        }
    }
    a{
        display:block;
        max-width:400px;
        padding:12px 24px;
        font-size:1.6rem;
        border:solid 1px rgba($line, .5);
        background-color:$white;
        @include transition(all .25s ease);
        @include border-radius(50px);
        &:hover{
            background-color:$primary;
            color:$white;
            text-decoration:none;
        }
        @include respon-to($screen-small) {
            font-size:1.6rem;
            padding:15px 28px;
        }
    }
}

@include respon-to($screen-small) {
    .detail-bank-page,
    .welcome-page,
    .transfer-page,
    .page-404-page {
        .header {
            position: relative;
        }
    }
}

.page-404-page{
    .image-banner{
        text-align:center;
        margin:0 auto 20px;
        img{
            display:block;
            margin:0 auto;
        }
        @include respon-to($screen-small) {
            margin-bottom:15px;
        }
    }
}