/* ==========================================================================
   PARAGRAPH
   ========================================================================== */

p{
    font-size:2.4rem;
    line-height:1.33;
    letter-spacing:0.23px;
    font-weight:$reguler;
    margin:0 0 2rem;
    @include respon-to($screen-small) {
        font-size:2.1rem;
        letter-spacing:0.16px;
        line-height:1.42;
    }
}

@include respon-to($screen-small) {
    .detail-bank-page{
        p{
            font-size: 1.6rem;
            &.amount {
              margin-bottom: 2rem;
            }
        }
    }
}


.color-primary{
    color:$primary;
}

.color-white{
    color:$white;
}