/* ==========================================================================
   VARIABLES
   ========================================================================== */

/* COLOR
   ========================================================================== */
   $white                 : #fff;
   $black                 : #000;
   $primary               : #048573;
   $text                  : #383838;
   $input-label           : #888;
   $line                  : #d5d5d5;
   $border-popup          : #ebebeb;
   $text-dropdown         : #6f6f6f;
   $text-dark-bg          : #e3e3e3;
   $grey-block            : #f2f2f2;
   $grey-note             : #9b9b9b;
   $body                  : #f3f3f3;
   $hover-link            : #0d8b79;
   $dark-bg               : #011e39;
   $dark-box              : #02192f;
   $error-message         : #d3002c;

/* BUTTON
   ========================================================================== */
   $button-left           : #146a6c;
   $button-right          : #0d8b79;


/* FONT WEIGHT
   ========================================================================== */
   $reguler               : 400;
   $bold                  : 700;

/* SCREEN
   ========================================================================== */
   $screen-large          : 1180px !default;
   $screen-medium         : 980px !default;
   $screen-small          : 767px !default;
   $screen-extra-small    : 480px !default;
   $screen-retina         : 320px !default;

   $max-screen            : 64rem;
   $min-screen            : 32rem;


/* GRID SYSTEM
   ========================================================================== */
   $grid-columns          : 12 !default;
   $grid-gutter-width     : 20px !default;

/* CONTAINER
   ========================================================================== */
   $container-large        : (1160px + $grid-gutter-width) !default;
   $container-medium       : (940px + $grid-gutter-width) !default;
   $container-small        : (720px + $grid-gutter-width) !default;
   $container-extra-small  : 540px !default;

/* Z-INDEX
   ========================================================================== */
   $z-header               : 999;
   $z-overlay              : 1000;
   $z-popup                : $z-overlay+1;