/* ==========================================================================
   PRELOADER
   ========================================================================== */

.preloader{
    position:fixed;
    top:0;
    left:0;
    width:100%;
    height:100%;
    z-index:999;
    background-color:rgba($white,.6);
    display:none;
    .preloader-icon{
        display:inline-block;
        position:absolute;
        top:53%;
        left:50%;
        width:200px;
        height:40px;
        background:url(../images/icon/preloader.svg) no-repeat center center;
        @include transform(translateY(-50%) translateX(-50%));
    }
}