/* ==========================================================================
   CONTENT
   ========================================================================== */

.bg-theme{
    width:100%;
    height:100%;
    background-image:url(../images/background/body-desktop.png);
    background-position:center center;
    background-size:1440px auto;
    background-repeat:no-repeat;
    @include respon-to($screen-small) {
        display:none;
    }
}

.site-content{
    position:absolute;
    top:45%;
    left:50%;
    padding:50px 0 0;
    z-index:1;
    @include transform(translateX(-50%) translateY(-50%));
    @include respon-to($screen-small) {
        padding-top:0;
        position:static;
        width:100%;
        min-height:100%;
        background-color:$white;
        @include transform(none);
    }
}

.main-content{
    width:648px;
    min-height:550px;
    background-color:rgba($white, .5);
    position:relative;
    @include box-shadow(0 2px 10px 0 rgba($black,.1));
    @include border-radius(15px);
    @include respon-to($screen-small) {
        width:100%;
        min-height:initial;
        position:static;
        padding:122px 20px 90px;
        background-color:transparent;
        @include border-radius(0);
        @include box-shadow(none);
    }
    @include respon-to($screen-retina) {
        padding-top:114px;
        padding-bottom:86px;
    }
}

@include respon-to($screen-small) {
    .welcome-page,
    .transfer-page,
    .page-404-page,
    .detail-bank-page {
        .main-content {
            padding: 24px 20px 90px;
        }
    }
}

.container-content{
    padding:70px 73px 140px;
    @include respon-to($screen-small) {
        padding:0;
    }
}

// character-illustration
.character-illustration{
    display:block;
    background-position:center center;
    background-repeat:no-repeat;
    background-size:contain;
    position:absolute;
    @include respon-to($screen-small) {
        display:none;
    }
}

.character-illustration-left{
    width:420px;
    height:521px;
    top:-55px;
    left:-380px;
    background-image:url(../images/background/character-illustration-left.png);
}
.character-illustration-right{
    width:591px;
    height:325px;
    bottom:-135px;
    right:-410px;
    background-image:url(../images/background/character-illustration-right.png);
}