/* ==========================================================================
   TABLE
   ========================================================================== */

table{
    margin:0 0 2rem;
    border-collapse:collapse;
    th,td{
        border:solid 1px $line;
    }
    th{
        padding:12px 16px;
        color:$white;
        background-color:$primary;
    }
    td{
        padding:12px 16px;
    }
}