/* ==========================================================================
   TRANSFER SUCCESS
   ========================================================================== */

.transfer-page{
    .bank-detail{
        a{
            color:$text;
        }
    }
    .container-content{
        padding:50px 73px 100px;
        @include respon-to($screen-small) {
            padding:0;
        }
    }
    .bank-account-name,.bank-detail{
        padding-bottom:10px;
        @include respon-to($screen-small) {
            padding-bottom:0;
        }
    }
    .transfer-icon{
        text-align:center;
        margin-bottom:40px;
        img{
            height:140px;
        }
        @include respon-to($screen-small) {
            text-align:left;
            margin-bottom:30px;
        }
    }
    .transfer-note{
        padding-right:60px;
        p{
            color:$grey-note;
            font-size:1.5rem;
            line-height:1.33;
            letter-spacing:0.12px;
        }
    }
}
