/* ==========================================================================
   DETAIL BANK
   ========================================================================== */

.detail-bank-page{
    @media screen and (max-height: 840px){
        min-height:880px;
        @include respon-to($screen-small) {
            min-height:initial;
        }
    }
    .top-sub-head{
        margin-bottom:4px;
    }
    p.amount{
        font-size:3.2rem;
        margin-bottom:1.6rem;
        @include respon-to($screen-small) {
            font-size:2.5rem;
        }
    }
    .bank-account-name{
        font-size:2.5rem;
        letter-spacing:0.19px;
        margin-top:8px;
        opacity:0;
        visibility:hidden;
        @include transition(all .1s ease);
        &.show{
            opacity:1;
            visibility:visible;
        }
    }
    .detail-bank-form{
        padding-top:3.5rem;
        @include respon-to($screen-small) {
            padding-top:0;
        }
    }
    .input-row{
        margin-bottom:0;
        @include respon-to($screen-small) {
            margin-bottom:1rem;
            margin-top:1.5rem;
        }
    }
    .bank-reset-form{
        font-size:1.5rem;
        color:$grey-note;
        margin-top:2rem;
        margin-bottom:1rem;
        opacity:0;
        visibility:hidden;
        @include transition(all .1s ease);
        &.show{
            opacity:1;
            visibility:visible;
        }
        .not-this-account{
            cursor:pointer;
        }
    }
}
.popup-bank-data{
    position:absolute;
    z-index:2;
    top:50px;
    max-height:240px;
    right:10px;
    left:10px;
    background-color:$white;
    overflow-x:hidden;
    overflow-y:scroll;
    -webkit-overflow-scrolling:touch;
    visibility:hidden;
    opacity:0;
    z-index:9999;
    @include transform(translateY(8px));
    @include border-radius(8px);
    @include box-shadow(0 5px 7px 0 rgba(163,164,169,.5));
    @include transition(all .15s ease);
    @include respon-to($screen-small) {
        position:fixed;
        top:0;
        left:0;
        right:0;
        height:100%;
        max-height:initial;
        opacity:1;
        @include transform(translateY(100%));
        @include box-shadow(none);
        @include transition(all .25s ease);
    }
    &.show{
        visibility:visible;
        opacity:1;
        @include transform(translateY(0));
        @include transition(all .25s ease);
        @include respon-to($screen-small) {
            @include transform(translateY(0));
            @include transition(all .35s ease);
        }
    }
    .popup-header{
        display:none;
        padding:17px 60px 17px 15px;
        position:relative;
        z-index:2;
        border-bottom:solid 1px $grey-block;
        background-color:$white;
        .input-text{
            padding-top:11px;
            padding-bottom:11px;
            padding-left:15px;
            padding-right:15px;
            border:0;
            background-color:$grey-block;
            @include border-radius(20px);
        }
        .popup-close{
            position:absolute;
            top:22px;
            right:20px;
            width:24px;
            height:24px;
            text-indent:-9999px;
            cursor:pointer;
            background:url(../images/icon/close-grey.svg) no-repeat center center;
        }
        @include respon-to($screen-small) {
            display:block;
        }
    }
    .popup-body{
        .error-message{
            padding:5px 30px;
            text-align:center;
            display:none;
            opacity:1;
            visibility:visible;
            @include respon-to($screen-small) {
            }
        }
        @include respon-to($screen-small) {
            position:absolute;
            z-index:1;
            top:74px;
            bottom:0;
            left:0;
            right:0;
            width:100%;
            overflow-y:scroll;
            -webkit-overflow-scrolling:touch;
            .error-message{
                padding:5px 30px;
                text-align:center;
            }
        }
    }
    .bank-list{
        padding:0;
        .item{
            padding:14px 12px;
            font-size:1.5rem;
            position:relative;
            cursor:pointer;
            color:$text-dropdown;
            @include transition(all .15s ease);
            &:after{
                content:"";
                display:block;
                position:absolute;
                bottom:0;
                left:0;
                right:0;
                opacity:1;
                border-bottom:solid 1px $line;
            }
            &.selected,
            &:hover{
                background-color:$grey-block;
                border-color:transparent;
            }
            @include respon-to($screen-small) {
               font-size:1.6rem;
               padding:20px 30px;
                &:after{
                    right:20px;
                    left:20px;
                }
            }
        }
    }
}