.wrapper-button{
    position:absolute;
    bottom:70px;
    @include respon-to($screen-small) {
        width:100%;
        position:fixed;
        bottom:0;
        left:0;
    }
}

.button{
    outline:0;
    border:0;
    min-width:175px;
    display:inline-block;
    text-decoration:none;
    text-align:center;
    padding:13px 34px 15px;
    color:$white;
    font-size:1.6rem;
    line-height:1.25;
    font-weight:$reguler;
    letter-spacing:0.06px;
    background-color:$primary;
    cursor:pointer;
    overflow:hidden;
    position:relative;
    @include border-radius(24px);
    @include transition(all .25s ease);
    @include box-shadow(none);
    &:hover{
        color:$white;
        background-color:$hover-link;
        text-decoration:none;
    }
    &.button-primary{
        @include background-image(linear-gradient(-45deg, $button-left 0%, $button-right 100%));
    }
    &.button-disabled{
        @include background-image(linear-gradient(-45deg, #8ab5b6 0%, #86c5bd 100%));
        cursor:default;
    }
    @include respon-to($screen-small) {
        display:block;
        width:100%;
        padding:23px 22px;
        text-align:left;
        font-weight:$bold;
        letter-spacing:0.06px;
        @include border-radius(0);
        &:after{       
            content:"";
            width:22px;
            height:22px;
            position:absolute;
            top:24px;
            right:20px;
            background:url(../images/icon/arrow-right.svg) no-repeat center center;
        }
    }
}