/* ==========================================================================
   HEADER
   ========================================================================== */

.header{
    text-align:center;
    margin-bottom:70px;
    .permata-bank-logo{
        display:inline-block;
        img{
            height:55px;
        }
    }
    @include respon-to($screen-large) {
        margin-bottom:60px;
    }
    @include respon-to($screen-small) {
        position:fixed;
        width:100%;
        top:0;
        z-index:999;
        padding:28px 0 22px;
        margin-bottom:0;
        background-color:$white;
        .permata-bank-logo{
            img{
                height:42px;
                display:block;
                margin:0 auto;
            }
        }
    }
    @include respon-to($screen-retina) {
        padding:24px 0 20px;
    }
}