/* ==========================================================================
   BASE
   ========================================================================== */

label, button{
	-webkit-tap-highlight-color:rgba($black, 0);
}

button,input,textarea{
	-webkit-appearance:none;
	font-family: 'Lato', sans-serif;
	font-size:100%;
	border:0;
	outline:0;
}

::-webkit-input-placeholder {
	font-family: 'Lato', sans-serif;
}
:-moz-placeholder {
	font-family: 'Lato', sans-serif;
}
::-moz-placeholder {
	font-family: 'Lato', sans-serif;
}
:-ms-input-placeholder {
	font-family: 'Lato', sans-serif;
}

input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
	-webkit-appearance:none;
	-moz-appearance:none;
	appearance:none;
	margin:0; 
}

html{
	height:100%;
	font-size:62.5%;
	@include respon-to($screen-retina) {
		font-size:55%;
		margin:0;
		height:100%;
		overflow:hidden;
	}
}

body{
	width:100%;
	height:100%;
	min-height:768px;
	position:relative;
	margin:0;
	padding:0;
	overflow:hidden;
	overflow-y:auto;
	font-family: 'Lato', sans-serif;
	font-weight:$reguler;
	font-size:2.1rem;
	line-height:1.38;
	color:$text;
	background-color:$white;
	&.no-transition{
		*{
			@include transition(none !important);
		}
	}
	&.remove-scroll{
		overflow:hidden;
	}
	&.remove-scroll-ios{
		overflow:hidden;
		position:fixed;
	}
	@include respon-to($screen-small) {
		min-height:initial;
	}
	@include respon-to($screen-retina) {
		margin:0;
		height:100%;
		overflow:hidden;
	}
}

a {
	color:$primary;
	cursor:pointer;
	text-decoration:none;
	&:hover{
		text-decoration:underline;
		color:$hover-link;
	}
}
a img {
	border:none;
}
input, textarea {
	outline:none
}
textarea {
	resize:none;
	overflow:auto
}
strong, b {
	font-weight:$bold;
}
em, i{
	font-style:italic
}

u {
	text-decoration:underline
}

img{
	max-width:100%;
	font-size:1.8rem;
}

*,*::after,*::before {
	@include box-sizing(border-box);
	-webkit-text-stroke-width:0;
	-webkit-text-stroke-color:transparent;
}