/* ==========================================================================
   HEADING
   ========================================================================== */

h1,h2,h3,h4,h5,h6{
    margin:0 0 2rem;
    line-height:1.2;
}


h1{
    font-size:3.6rem;
    font-weight:$bold;
}

h2{
    font-size:2.8rem;
    font-weight:$bold;
}

h3{
    font-size:2.4rem;
    font-weight:$bold;
}

h4{
    font-size:1.8rem;
    font-weight:$bold;
}

h5{
    font-size:1.6rem;
    font-weight:$bold;
}

h6{
    font-size:1.2rem;
    font-weight:$bold;
}