/* ==========================================================================
   RESEND CODE
   ========================================================================== */

.resend-code-page{
    background-color:$dark-bg;
    color:$white;
    .preloader{
        background-color:rgba($dark-bg, .6);
    }
    .bg-theme{
        opacity:.16;
    }
    .site-content{
        @include respon-to($screen-small) {
            background-color:$dark-bg;
        }
    }
    .header{
        display:none;
    }
    .main-content{
        background-color:rgba($dark-box, .54);
        @include respon-to($screen-small) {
            background-color:transparent;
        }
    }
    .container-content{
        padding:50px 73px 140px 73px;
        @include respon-to($screen-small) {
            padding:0;
        }
    }
    .resend-code-icon{
        text-align:center;
        img{
            display:block;
            margin:0 auto 1.5rem;
            height:91px;
        }
        .icon-initialize{
            display:block;
            &.hide{
                display:none;
            }
            &.show{
                display:block;
            }
        }
        .icon-error-otp{
            display:none;
            &.hide{
                display:none;
            }
            &.show{
                display:block;
            }
        }
    }
    .resend-code-title{
        text-align:center;
        h5{
            margin-bottom:15px;
            span{
                font-weight:$reguler;
            }
        }
    }
    .resend-code-error-otp{
        font-size:1.3rem;
        line-height:1.31;
        text-align:center;
        margin-bottom:15px;
        opacity:0;
        visibility:hidden;
        color:$error-message;
        @include transition(all .15s ease);
        &.show{
            opacity:1;
            visibility:visible;
        }
    }
    .resend-code-body{
        text-align:center;
        p{
            font-size:1.8rem;
            line-height:1.61;
            letter-spacing:0.14px;
            color:rgba($text-dark-bg,.5);
            span.color-white{
                color:$white;
            }
        }
    }
    .input-row{
        border-bottom:solid 1px rgba($line, .7);
    }
    .input-text{
        padding:0 0 10px;
        margin-left:8px;
        font-size:2.1rem;
        line-height:2;
        font-weight:$bold;
        color:$white;
        text-align:center;
        letter-spacing:15px;
        border-bottom:0;
    }
    .resend-code-button{
        text-align:center;
        margin-top:30px;
        position:absolute;
        bottom:155px;
        width:100%;
        left:0;
        .resend-button{
            outline:0;
            border:0;
            cursor:pointer;
            background-color:transparent;
            font-size:1.6rem;
            font-weight:$bold;
            color:$white;
            text-decoration:none;
            &:hover{
                color:$white;
                text-decoration:none;
            }
        }
        .close-button{
            display:none;
            outline:0;
            border:0;
            cursor:pointer;
            background-color:transparent;
            font-size:1.6rem;
            font-weight:$bold;
            color:$white;
            text-decoration:none;
            width:32px;
            height:32px;
            text-indent:-9999px;
            cursor:pointer;
            background:url(../images/icon/close.svg) no-repeat center center;
            &:hover{
                color:$white;
                text-decoration:none;
            }
        }
        @include respon-to($screen-small) {
            top:0;
            z-index:999;
            padding:30px 0;
            bottom:auto;
            margin-top:0;
            @include clearfix;
            .resend-button{
                float:left;
                margin-top:3px;
                padding:4px 5px;
                margin-left:15px;
            }
            .close-button{
                display:block;
                float:right;
                margin-right:15px;
            }
        }
    }
    .wrapper-button{
        position:absolute;
        bottom:70px;
        text-align:center;
        width:100%;
        left:0;
        @include respon-to($screen-small) {
            width:100%;
            position:fixed;
            bottom:0;
            left:0;
        }
    }
}